export const changePageNumber = (number) => {
  return {
    type: "CHANGE_PAGE_NUMBER",
    payload: number,
  };
};

export const mouseEnterCornerVideo = (ref) => {
  return {
    type: "MOUSE_ENTER_CORNER_VIDEO",
    payload: ref,
  };
};

export const hoveredChange = (bool) => {
  return {
    type: "HOVERED",
    payload: bool,
  };
};
